/* SLIDE */

.Slide {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 70px;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

    [data-night-mode="false"] .Slide {
        color: black;
    }

    .Slide:not(.is-active) {
        display: none;
    }

.Slide img {
    max-height: 100%;
    -o-object-fit: contain;
       object-fit: contain;
    background-color: rgba(125,125,125,0.05);
}

.Slide iframe {
    max-height: 100%;
    width: 800px;
    height: 600px;
}

.Slide-title {
    max-width: 100%;
    font-size: var(--sm-font-size);
    font-weight: normal;
}

.Slide-spacer {
    -ms-flex: 1 1;
        flex: 1 1;
}

.Slide-link {
    display: block;
}

.Slide-icon {
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    border-left: 2px solid var(--dark-grey);
    border-bottom: 2px solid var(--dark-grey);
}

.Slide-icon--download {
    bottom: 50px;
    right: 50px;
}

.Slide-icon--link {
    bottom: 10px;
    right: 10px;
}


/* CHANNEL SLIDE */

.Slide-link--channel {
    width: 425px;
    height: 425px;
    position: relative;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 20px;
    border: 3px solid;
    border-color: rgba(23,172,16,0.5);
    color: rgb(23,172,16);
}

    .Slide-link--channel:hover {
        border-color: rgba(23,172,16,1);
    }

    .Slide-link--channel.is-closed {
        border-color: rgba(72,62,100,0.5);
        color: rgb(72,62,100);
    }

    .Slide-link--channel.is-closed:hover {
        border-color: rgba(72,62,100,1);
    }

.Slide-channelTitle {
    -ms-flex: 0 1;
        flex: 0 1;
    font-weight: normal;
}

.Slide-channelStatList {
    font-size: var(--sm-font-size);
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0;
    list-style: none;
}


/* TEXT SLIDE */

.Slide-textBlock {
    font-family: Times, serif;
    max-width: 800px;
    line-height: 1.5;
    max-height: 100%;
    -webkit-user-select: text;
       -moz-user-select: text;
        -ms-user-select: text;
            user-select: text;
    cursor: text;
    font-size: var(--md-font-size);
}


/* NON-SPECIFIC MEDIA SLIDE */

.Slide-mediaStamp {
    background-color: var(--light-grey);
    color: var(--dark-grey);
    width: 425px;
    height: 425px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    padding: 20px;
    border-radius: 20px;
    max-width: 100%;
    position: relative;
    z-index: 2;
    border-left: 5px solid var(--dark-grey);
    border-bottom: 5px solid var(--dark-grey);
}

.Slide-mediaExtension {
    text-transform: uppercase;
    font-size: var(--xl-font-size);
    font-weight: normal;
}

.Slide-mediaTitle {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: var(--sm-font-size);
    font-weight: normal;
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
}


/* WEB SLIDE */

.Slide-link--web {
    max-width: 500px;
    text-align: center;
    position: relative;
}

    .Slide-link--web .Slide-title {
        opacity: 0.5;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
    }

    .Slide-link--web:hover .Slide-title {
        opacity: 1;
    }



